import React from 'react';
import {
  Button,
  ButtonGroup,
  Icon,
  Typography
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import Timer from './Timer';
import { capitalizeFirst } from '../../../_pxp/utils/Common';
import { useHistory } from 'react-router-dom';
// REDUX
import { useSelector, useDispatch } from 'react-redux';
import { updateCitaStateAction } from '../../redux/citas-actions';

// Moment
import moment from 'moment';
import 'moment-precise-range-plugin';
import 'moment/locale/es';
import { parseDateDiff } from './timer-parse';
moment.locale('es');

const useStyles = makeStyles(theme => ({
  messageContent: {
    textAlign: 'center',
    marginTop: '0.8rem',
    marginBottom: '0.8rem',
  },
  message: {
    fontSize: '1rem',
  },
  label: {
    fontSize: '0.6rem',
  }
}));

const Actions = ({ options = [], data = {} }) => {
  const classes = useStyles();
  const history = useHistory();
  // const { data: dataService, setParams, error } = useServicesDama();
  const dispatch = useDispatch();

  const changeState = (nextState) => {
    dispatch(updateCitaStateAction(
      data.id_cita,
      nextState,
      data.latitud_ubicacion,
      data.longitud_ubicacion
    ));
  };

  const goTo = (url) => history.push(url + data.id_cita);

  return (
    <ButtonGroup variant="contained" color="primary" fullWidth>
      {options.map(option => (
        <Button
          key={option.label}
          onClick={option.nextState ? () => changeState(option.nextState) : () => goTo(option.url)}
          color={option.color || 'primary'}
          startIcon={<Icon>{option.icon}</Icon>}
          className={classes.label}
        >
          {option.label}
        </Button>
      ))}
    </ButtonGroup>
  );
};

const statesDama = {
  disponible: 'si',
  noDisponible: 'no',
};

const statesCita = {
  cancelada: 'cancelada',
  completada: 'completada',
  confirmada: 'confirmada',
  finalizada: 'finalizada',
  iniciada: 'iniciada',
  panico: 'panico',
  programada: 'programada',
  sinCita: 'sin cita',
  solicitada: 'solicitada',
  rechazada: 'rechazada',
};

const setVariables = (dataDama = {}) => {
  const { disponible, credito, credito_minimo } = dataDama;
  const stateDama = disponible === statesDama.disponible ? true : false;
  const creditValid = parseFloat(credito) < parseFloat(credito_minimo) ? false : true;

  return {
    disponible,
    stateCita: dataDama.estado_cita,
    credito,
    credito_minimo,
    alias: dataDama.nombre_dama,
    stateDama,
    creditValid
  }
};

const commonButtons = {
  goToCita: {
    label: 'IR A LA CITA',
    icon: 'date_range',
    // color: 'secondary',
    url: '/citas/detail/',
  },
  confirmCita: {
    label: 'CONFIRMAR CITA',
    icon: 'thumb_up_alt',
    color: 'secondary',
    nextState: statesCita.programada
  },
  cancelCita: {
    label: 'CANCELAR CITA',
    icon: 'thumb_down_alt',
    nextState: statesCita.rechazada
  },
  startCita: {
    label: 'INICIAR CITA',
    icon: 'date_range',
    nextState: statesCita.iniciada
  },
  panic: {
    label: 'Activar Panico',
    icon: 'campaign',
    nextState: statesCita.panico
  },
  endCita: {
    label: 'Finalizar Cita',
    icon: 'cancel',
    color: 'secondary',
    nextState: statesCita.finalizada
  },
  error: {
    label: 'FUE UN ERROR',
    color: 'secondary',
    icon: 'sentiment_very_satisfied',
    nextState: statesCita.iniciada,
  },
  endPanic: {
    label: 'Finalizar Cita',
    icon: 'sentiment_dissatisfied',
    nextState: statesCita.completada,   // Analizar posible cambio segun se presiono o use el boton de PANICO
  },
  completeCita: {
    label: 'Completar Cita',
    color: 'secondary',
    icon: 'check',
    nextState: statesCita.completada
  }
};

const viewState = (data = {}) => {
  const { alias, fecha_cita, hora_cita, cliente_cita, fecha_hora_estado_cita } = data;

  const name = `<b>${capitalizeFirst(alias)}</b>`;
  const cliente = `<b>${capitalizeFirst(cliente_cita)}</b>`;
  const date = moment(fecha_cita + ' ' + hora_cita, 'YYYY-MM-DD HH:mm:ss');
  const diff = date.preciseDiff(moment(), true);
  const diffState = moment().preciseDiff(moment(fecha_hora_estado_cita), true);
  const stringDiff = parseDateDiff(diff, true);
  const states = {};

  states.solicitadaCredit = {
    actions: [
      commonButtons.cancelCita,
      commonButtons.confirmCita,
      commonButtons.goToCita
    ],
    message: `${name}, tiene solicitada una cita de ${cliente}, sin confirmar para el 
      <b>${date.format('dddd, LL [a las] LT')}</b> ¿Qué desea hacer?.`
  };

  states.solicitadaNoCredit = {
    actions: null,
    message: `${name}, tiene solicitada una cita de ${cliente}, sin confirmar y no tiene credito suficiente para confirmarla. 
      Contactese con el administrador para recargar mas credito.`
  };

  states.programada = {
    actions: [
      { ...commonButtons.cancelCita, nextState: statesCita.cancelada },
      { ...commonButtons.confirmCita, nextState: statesCita.confirmada },
    ],
    message: `${name}, usted tiene una cita programada con ${cliente}, dentro de: `,
    timer: {
      timer: true,
      time: diff
    }
  };

  states.confirmada = {
    actions: [
      commonButtons.startCita
    ],
    message: `${name}, su cita con ${cliente}, fue confirmada hace: `,
    timer: {
      timer: false,
      time: diffState
    }
  };

  states.iniciada = {
    actions: [
      commonButtons.panic,
      commonButtons.endCita,
    ],
    message: `${name}, su cita con ${cliente}, fue iniciada hace:`,
    timer: {
      timer: false,
      time: diffState,
      size: 'large',
      type: 'digital',
      alarmInterval: 2 * 60
    }
  };

  states.panico = {
    actions: [
      commonButtons.error,
      commonButtons.endPanic,
    ],
    message: `${name}, hemos recibido su solicitud de ayuda!!!`,
  };

  states.finalizada = {
    actions: [
      commonButtons.panic,
      commonButtons.completeCita,
    ],
    message: `${name}, complete la cita con ${cliente}, una vez que se encuentre segura.`,
  };

  states.sinCitaCreditDama = {
    actions: null,
    message: `${name}, se encuentra disponible para recibir citas. 
      Por favor permanezca atenta a nuevas solicitudes y verifique que su ubicación es la correcta.`,
  };

  states.sinCitaCreditNoDama = {
    actions: null,
    message: `${name}, no te encuentras disponible en este momento. 
      Ponte disponible para que mas clientes te puedan contactar.`,
  };

  states.sinCitaNoCreditNoDama = {
    actions: null,
    message: `${name}, no tiene credito suficiente para estar disponible en la aplicación. 
      Contactese con el administrador para recargar mas credito`,
  };

  return states;
};
export const stateOptions = (dataDama) => {
  const {
    stateCita,
    alias,
    stateDama,
    creditValid
  } = setVariables(dataDama);

  const currentState = viewState({ ...dataDama, alias });
  switch (stateCita) {
    case statesCita.solicitada:
      return creditValid ? currentState.solicitadaCredit : currentState.solicitadaNoCredit;
    case statesCita.programada:
      return currentState.programada;
    case statesCita.confirmada:
      return currentState.confirmada;
    case statesCita.iniciada:
      return currentState.iniciada;
    case statesCita.panico:
      return currentState.panico;
    case statesCita.finalizada:
      return currentState.finalizada;
    case statesCita.sinCita: {
      if (stateDama && creditValid) { // VALIDAR CREDITO >= 50bs
        return currentState.sinCitaCreditDama;
      } else if (creditValid && !stateDama) {
        return currentState.sinCitaCreditNoDama;
      } else {
        return currentState.sinCitaNoCreditNoDama;
      }
    };
    default: return {
      actions: null,
      message: `Sin datos, para una próxima cita.`
    };
  }
};

export const StateDama = ({ data = {} }) => {
  const classes = useStyles();
  const { actions = null, message, timer = null } = stateOptions({ ...data });

  return (
    <>
      <p className={classes.messageContent}>
        <Typography variant="caption" color="textSecondary" className={classes.message}>
          <span dangerouslySetInnerHTML={{ __html: message }} />

        </Typography>
      </p>
      {timer &&
        <Timer timer={timer.timer}
          time={timer.time}
          size={timer.size}
          type={timer.type}
          alarmInterval={timer.alarmInterval}
        />}
      {actions && <Actions options={actions} data={data} />}
    </>
  )
};

export const StateDamaChat = ({ data = {} }) => {
  const { actions = null} = stateOptions({ ...data });

  return <>{actions && <Actions options={actions} data={data} />}</>;
};
