export const parseDateDiff = ({ years, months, days, hours, minutes, seconds }, hideSeconds = false) => {
  var STRINGS = {
    nodiff: '',
    year: 'año',
    years: 'años',
    month: 'mes',
    months: 'meses',
    day: 'dia',
    days: 'dias',
    hour: 'hora',
    hours: 'horas',
    minute: 'minuto',
    minutes: 'minutos',
    second: 'segundo',
    seconds: 'segundos',
    delimiter: ' '
  };

  function pluralize(num, word) {
    return num + ' ' + STRINGS[word + (num === 1 ? '' : 's')];
  }

  function buildStringFromValues(yDiff, mDiff, dDiff, hourDiff, minDiff, secDiff) {
    var result = [];

    if (yDiff) {
      result.push(pluralize(yDiff, 'year'));
    }
    if (mDiff) {
      result.push(pluralize(mDiff, 'month'));
    }
    if (dDiff) {
      result.push(pluralize(dDiff, 'day'));
    }
    if (hourDiff) {
      result.push(pluralize(hourDiff, 'hour'));
    }
    if (minDiff) {
      result.push(pluralize(minDiff, 'minute'));
    }
    if (secDiff && !hideSeconds) {
      result.push(pluralize(secDiff, 'second'));
    }

    return result.join(STRINGS.delimiter);
  }

  return buildStringFromValues(years, months, days, hours, minutes, seconds);

};