import React, { useState, useEffect, useRef } from 'react';
import {
  Card,
  Grid,
  Typography,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Icon,
  ListItemSecondaryAction,
  Switch,
  Collapse,
  IconButton,
  CardContent,
  Box,
  CircularProgress,
} from '@material-ui/core';
import Chip from '@material-ui/core/Chip';
import { green } from '@material-ui/core/colors';
import { Scrollbars } from 'react-custom-scrollbars';
import { v4 as uuidv4 } from 'uuid';
import { removeWebSocketListener, webSocketListener } from 'pxp-client';
import { makeStyles } from '@material-ui/styles';
import Location from './Location';
import { StateDama } from './states-options';
import InfoPanel from './InfoPanel';
// REDUX
import { useSelector, useDispatch } from 'react-redux';
import {
  requestDataDama,
  updateDisponibilidadAction,
  updateLocationAction,
} from '../../redux/citas-actions';
import { receivedNotifyAction } from '../../../_pxp/actions/notify';
import Notifications from './Notifications';

const useStyles = makeStyles((theme) => ({
  root: {
    padding: theme.spacing(1),
  },
  title: {
    fontWeight: 600,
  },
  card: {
    boxShadow: theme.shadows[12],
  },
  message: {
    fontSize: '1rem',
    textAlign: 'center',
    marginTop: 0,
    marginBottom: '1rem',
  },
  content: {
    padding: '6px',
    '&:last-child': {
      paddingBottom: '6px',
    },
  },
  error: {
    margin: '0.3rem',
    textAlign: 'center',
  },
  progress: {
    color: green[100],
    position: 'absolute',
    top: 0,
    left: 12,
    zIndex: 1,
  },
}));

const uuid = uuidv4();

const Config = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const { dataDama, updateDataDama } = useSelector((state) => state.citas);
  const auth = useSelector((state) => state.auth);
  const notify = useSelector((state) => state.notify);
  const eventWs = `user_notifications__${auth.currentUser.id_usuario}`;
  const scrollRef = useRef(null);
  const [available, setAvailable] = useState(false);
  const [loadingCheck, setLoadingCheck] = useState(false);

  const [collapseMap, setCollapseMap] = useState(false);
  const [collapseNoti, setCollapseNoti] = useState(false);

  const toogleAvailable = () => {
    setLoadingCheck(true);
    dispatch(updateDisponibilidadAction(dataDama.id_dama));
    // setParams(damaServices.setDisponibilidad(dataDama.id_dama))
  };

  const changePosition = ({ lat, lng }) => {
    dispatch(updateLocationAction(dataDama.id_dama, lat, lng));
  };
  const toogleCollapse = () => {
    setCollapseMap(!collapseMap);
    setCollapseNoti(false);
  };

  const toogleCollapseNoti = () => {
    setCollapseNoti(!collapseNoti);
    setCollapseMap(false);
  };

  useEffect(() => {
    if (collapseMap || collapseNoti) {
      setTimeout(() => {
        // console.log(scrollRef.current);
        scrollRef.current.scrollToBottom(0);
      }, 300);
    }
  }, [collapseMap, collapseNoti]);

  useEffect(() => {
    if (!dataDama.loading && !dataDama.error) {
      setLoadingCheck(false);
      setAvailable(dataDama.disponible === 'si');
    }
  }, [dataDama]);

  useEffect(() => {
    console.log('[ACTIVE EVENT]', updateDataDama)
    if (updateDataDama) {
      dispatch(requestDataDama());
    }
  }, [updateDataDama]);

  useEffect(() => {
    console.log('[ACTIVE NEW]', notify.newNotify)
    if (notify.newNotify) {
      dispatch(requestDataDama());
      dispatch(receivedNotifyAction());
    }
  }, [notify]);

  return (
    <Scrollbars
      ref={scrollRef}
      autoHide
      renderView={(props) => (
        <div
          {...props}
          style={{ ...props.style, overflowX: 'hidden', marginBottom: 0 }}
        />
      )}
    >
      <div className={classes.root}>
        <Grid container spacing={2}>
          <ItemConfig title="Información">
            <CardContent className={classes.content}>
              <InfoPanel data={dataDama} />
              <StateDama data={dataDama} />
              {dataDama.error && (
                <Box className={classes.error}>
                  <Chip
                    icon={<Icon>close</Icon>}
                    label={dataDama.error}
                    color="primary"
                    variant="outlined"
                  />
                </Box>
              )}
            </CardContent>
          </ItemConfig>
          <ItemConfig title="Disponibilidad">
            <List disablePadding>
              <ListItem>
                <ListItemAvatar>
                  <Icon color="secondary">check_circle</Icon>
                </ListItemAvatar>
                <ListItemText
                  primary="Disponible"
                  secondary="Sus clientes, pueden ver esta información"
                />
                <ListItemSecondaryAction>
                  <Switch
                    edge="end"
                    checked={available}
                    onChange={toogleAvailable}
                    inputProps={{
                      'aria-labelledby': 'switch-list-label-bluetooth',
                    }}
                  />
                  {loadingCheck && (
                    <CircularProgress
                      size={36}
                      color="secondary"
                      className={classes.progress}
                    />
                  )}
                </ListItemSecondaryAction>
              </ListItem>
            </List>
          </ItemConfig>
          <ItemConfig title="Ubicación">
            <List disablePadding>
              <ListItem>
                <ListItemAvatar>
                  <Icon color="secondary">location_on</Icon>
                </ListItemAvatar>
                <ListItemText
                  primary="Ubicación Actual"
                  secondary="Mueva el mapa, para cambiar su ubicación"
                />
                <ListItemSecondaryAction>
                  <IconButton onClick={toogleCollapse}>
                    <Icon>
                      {collapseMap
                        ? 'keyboard_arrow_up'
                        : 'keyboard_arrow_down'}
                    </Icon>
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            </List>
            <Collapse in={collapseMap} timeout="auto">
              {dataDama.latitud_ubicacion && (
                <Location
                  changePosition={changePosition}
                  lat={dataDama.latitud_ubicacion}
                  lng={dataDama.longitud_ubicacion}
                />
              )}
            </Collapse>
          </ItemConfig>
          <ItemConfig title="Notificaciones">
            <List disablePadding>
              <ListItem>
                <ListItemAvatar>
                  <Icon color="secondary">notifications_none</Icon>
                </ListItemAvatar>
                <ListItemText
                  primary="Últimas Notificaciones"
                  secondary="Inforación sobre nuevas citas, creditos, débitos"
                />
                <ListItemSecondaryAction>
                  <IconButton onClick={toogleCollapseNoti}>
                    <Icon>
                      {collapseNoti
                        ? 'keyboard_arrow_up'
                        : 'keyboard_arrow_down'}
                    </Icon>
                  </IconButton>
                </ListItemSecondaryAction>
              </ListItem>
            </List>
            <Collapse in={collapseNoti} timeout="auto">
              <Notifications />
            </Collapse>
          </ItemConfig>
        </Grid>
      </div>
    </Scrollbars>
  );
};

const ItemConfig = ({ title, children }) => {
  const classes = useStyles();
  return (
    <Grid item xs={12}>
      <Typography variant="caption" color="primary" className={classes.title}>
        {title.toUpperCase()}
      </Typography>
      <Card className={classes.card}>{children}</Card>
    </Grid>
  );
};

export default Config;
