import React from 'react';
import {
  Box
} from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import clsx from 'clsx';
import useSound from 'use-sound';
import { parseDateDiff } from './timer-parse';

import alarm from '../../resourses/door.mp3';
import alarm2 from '../../resourses/notify_2.mp3';
import notify from '../../resourses/notify.mp3';

const useStyles = makeStyles(theme => ({
  timer: {
    padding: 0,
    textAlign: 'center',
    fontSize: '1rem',
  },
  small: {
    fontSize: '0.8rem',
  },
  large: {
    fontSize: '3.5rem',
  }
}));


const convertTime = (time) => {
  const h = Math.trunc(time / 3600);
  let untime = time % 3600;
  const m = Math.trunc(untime / 60);
  const s = (untime % 60);
  return {
    hours: h,
    minutes: m,
    seconds: s,
  };
};

const hourDigital = ({ hours, minutes, seconds }) => checkTime(hours) + ":" + checkTime(minutes) + ":" + checkTime(seconds);

function checkTime(i) {
  if (i < 10) {
    i = "0" + i;
  }
  return i;
}

const convertTimeSeconds = ({ hours, minutes, seconds }) => hours * 3600 + minutes * 60 + seconds;

const Timer = ({ timer, time, size, type = 'text', alarmInterval = null }) => {
  const classes = useStyles();
  const operator = timer ? -1 : 1;
  const [timerInterval, setTimer] = React.useState(convertTimeSeconds(time));
  const [isActive, setIsActive] = React.useState(true);

  // const alarm = '../../resourses/door.mp3';
  const [playOn] = useSound(
    alarm2,
    { volume: 1 }
  );
  const [notifyOn] = useSound(
    notify,
    { volume: 1 }
  );
  const timerRun = () => setInterval(() => setTimer(timerInterval + operator), 1000);


  React.useEffect(() => {
    let interval;
    if (isActive) {
      interval = timerRun();
    } else if (!isActive && timerInterval !== 0) {
      clearInterval(interval);
    }

    if (timerInterval === 0) {
      playOn();
      setIsActive(false);
    }

    if (alarmInterval && timerInterval % alarmInterval === 0) {
      notifyOn();
    }
    return () => clearInterval(interval);
  }, [timerInterval, isActive]);

  return (
    <Box className={clsx(classes.timer, classes[size])}>
      {type === 'digital' && <label>{hourDigital(convertTime(timerInterval))}</label>}
      {type === 'text' && <label>{parseDateDiff(convertTime(timerInterval))}</label>}
    </Box>
  )
};

export default Timer;
