import React from 'react';
import {
  Paper,
  Typography,
  Grid,
  Icon
} from '@material-ui/core'
import { makeStyles } from '@material-ui/styles';
import clsx from 'clsx';

const useStyles = makeStyles(theme => {
  return {
    root: {
      flexGrow: 1,
    },
    panel: {
      // flexGrow: 5,
      boxShadow: theme.shadows[8],
      backgroundColor: theme.palette.background.dark,
      borderRadius: '5px',
      padding: theme.spacing(2),
    },
    panelPrimary: {
      backgroundColor: theme.palette.primary.dark,
      border: `1px solid ${theme.palette.primary.light}`,
      color: `white`,
    },
    panelSecondary: {
      backgroundColor: theme.palette.secondary.dark,
      border: `1px solid ${theme.palette.secondary.light}`,
      color: `white`,
    },
    title: {
      fontSize: '5vw',
    }
  }
});

const InfoPanel = ({ data = {} }) => {
  const classes = useStyles();
  return (
    <Grid container className={classes.root} spacing={1} justify="space-between">
      <Grid item xs={6}>
        <Paper className={clsx(classes.panel, classes.panelPrimary)}>
          <Grid container justify="space-between" alignItems="center">
            <Grid item>
              <Typography variant="h6">
                CREDITO
              </Typography>
              <Typography variant="h2" className={classes.title}>
                {Math.trunc(data.credito) || '0'} Bs
              </Typography>
            </Grid>
            <Grid item xs={2}>
              <Icon fontSize="large">attach_money</Icon>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
      <Grid item xs={6}>
        <Paper className={clsx(classes.panel, classes.panelSecondary)}>
          <Grid container justify="space-between" alignItems="center">
            <Grid item>
              <Typography variant="h6">
                ESTADO
            </Typography>
              <Typography variant="h2" className={classes.title}>
                {data.estado_cita ? data.estado_cita : (data.disponible === 'si' ? 'Disponible' : 'No disponible')}
              </Typography>
            </Grid>
            <Grid item xs={2}>
              <Icon fontSize="large">info</Icon>
            </Grid>
          </Grid>
        </Paper>
      </Grid>
    </Grid>

  )
}

export default InfoPanel;
