import React from 'react';
import { GoogleApiWrapper } from 'google-maps-react';
import { Map, InfoWindow, Marker } from 'google-maps-react';

import { makeStyles } from '@material-ui/styles';
import { Box } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
  root: {
    position: 'relative !important',
    width: '100%',
    height: '100%',
  },
  mapContainer: {
    position: 'relative !important',
    width: '100%',
    height: '100%',
    minHeight: '450px',
    // border: '1px solid ' + theme.palette.secondary.light,
  },
  markerFixed: {
    left: '50%',
    marginLeft: -24,
    marginTop: -48,
    position: 'absolute',
    top: '50%',
    zIndex: 1,
  },
  marker: {
    height: 48,
    width: 48
  },
}));

export const MapContainer = ({ google, changePosition, lat = 0, lng = 0 }) => {
  const classes = useStyles();
  const [location] = React.useState({
    lat: parseFloat(lat),
    lng: parseFloat(lng),
  });
  const onMarkerClick = () => { console.log('marker') };
  const onInfoWindowClose = () => { console.log('info') };
  const markerDargEnd = (e, map) => {
    changePosition({
      lat: map.getCenter().lat(),
      lng: map.getCenter().lng(),
    });
  };

  const centerMoved = (mapProps, map) => {
    console.log('drag end', mapProps, map)
  };

  return (
    <Box className={classes.root}>
      <div className={classes.markerFixed}>
        <img className={classes.marker} src="https://cdn0.iconfinder.com/data/icons/small-n-flat/24/678111-map-marker-512.png" />
      </div>
      <Map google={google} zoom={16} className={classes.mapContainer}
        // centerAroundCurrentLocation
        initialCenter={location}
        onDragend={centerMoved}
        fullscreenControl={false}
        mapTypeControl={false}
        streetViewControl={false}
        onDragend={markerDargEnd}
      >

        {
          // <Marker onClick={onMarkerClick}
          //   name={'Current location'}
          //   draggable={true}
          //   onDragend={markerDargEnd}
          //   position={{ lat: -17.985836, lng: -67.1120607 }}
          // />
        }

        <InfoWindow onClose={onInfoWindowClose} visible={false}>
          <div>
            <h1>{'Demo'}</h1>
          </div>
        </InfoWindow>
      </Map >
    </Box>

  );
}

export default GoogleApiWrapper({
  apiKey: 'AIzaSyB_AyQ023yUEar_t9jpGXhPAWuj_Dfar7s'
})(MapContainer);

